.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.team{
  background-color: rgb(248, 110, 29);
    padding: 8px;
    text-decoration: none;
    color: white;
}
.team:hover{
color:white;
cursor:alias;
}
.game
{
  color: #131212;
    padding: 8px;
    text-decoration: none;
    box-shadow: 2px 1px 2px 0px #9a9a9a;
}
.game:hover{
  color:black;
  cursor:alias;
}
